/* HOME STYLES */
.root {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    height: 100vh;
    width: 100%;
    z-index: 0;
    position: absolute;
    left: 0;
    top: 0;
    background-image: url('https://source.unsplash.com/OZ2PtmqXckk/1600x900');
    background-position-x: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .white-btn {
    position: absolute;
    top: 60%;
    left: 55%;
    z-index: 1;
    padding: 0.3em 2em;
    font-size: 1.1rem;
    font-weight: bold;
    font-family: inherit;
    cursor: pointer;
    color: black;
    outline: none;
    border: none;
    background-color: white;
    text-transform: uppercase;
  }
  .github {
    font-size: 2rem;
    cursor: pointer;
    color: white;
    padding: 1rem;
    text-decoration: none;
  }
  
  /* Extra small devices (portrait phones, less than 576px) */
  @media (max-width: 575.98px) {
    .white-btn {
      top: 75%;
      left: 32%;
    }
    .github {
      font-size: 1.5rem;
    }
  }