/* CART ITEM STYLES */
.root {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
  }
  
  .info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    height: 8rem;
  }
  .info a {
    color: black;
    text-decoration: none;
    font-weight: 500;
    text-transform: uppercase;
  }
  
  .qty-btns {
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
  }
  .qty-btns button {
    background-color: white;
    border: 2px solid black;
    padding: 0 0.5rem;
    color: black;
    font-family: inherit;
    font-size: 1rem;
    font-weight: 500;
  }
  .qty-btns span {
    background-color: white;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    width: 2rem;
    text-align: center;
    color: black;
    font-family: inherit;
    font-size: 1rem;
    font-weight: 500;
  }
  
  .delete-btn {
    background-color: white;
    color: black;
    font-family: inherit;
    font-size: 0.8rem;
    border: none;
    cursor: pointer;
  }
  .delete-btn:hover {
    text-decoration: underline;
  }
  
  .responsive-image {
    max-width: 100%;
    height: auto;
  }